import { HttpEventType, HttpResponse } from '@angular/common/http';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { ApiService } from 'src/app/services/api.service';
import { createWorker } from 'tesseract.js';
import { Location } from '@angular/common';
import { ModalController } from '@ionic/angular';
import { CropComponent } from 'src/app/components/crop/crop.component';

const worker = createWorker({
  logger: (m) => console.log(m),
});

@Component({
  selector: 'app-file-upload',
  templateUrl: './file-upload.page.html',
  styleUrls: ['./file-upload.page.scss'],
})
export class FileUploadPage implements OnInit {
  isFile: boolean = false;
  filename: string;
  base64File: any;
  files: any = [];
  percentDone: number;
  uploadSuccess: boolean;
  isUploading: boolean = false;
  uploadingFile: string;
  requestSubscription: Subscription;
  parsedText: any;
  chequeData;

  constructor(
    private router: Router,
    private api: ApiService,
    private cdr: ChangeDetectorRef,
    private _location: Location,
    private modalCtrl: ModalController
  ) {}

  ngOnInit() {}

  goBack() {
    localStorage.setItem('chequeData', JSON.stringify(this.chequeData));
    this._location.back();
  }

  onFileSelect(e: any, side: any): void {
    try {
      const file = e.target.files[0];
      this.openCropComponent(file);
      // const fReader = new FileReader()
      // fReader.readAsDataURL(file)
      // fReader.onloadend = (_event: any) => {
      //   this.filename = file.name;
      //   this.base64File = _event.target.result;
      //   console.log(this.filename);
      //   this.files.push(file);
      //   this.readChequeData(file);
      //   // this.imageParser(file);
      //   this.fileUpload(file);
      // }
    } catch (error) {
      this.filename = null;
      this.base64File = null;
    }
  }

  // imageParser(image: File) {
  //   (async () => {
  //     await worker.load();
  //     await worker.loadLanguage('eng');
  //     await worker.initialize('eng');
  //     const { data: { text } } = await worker.recognize(image);
  //     this.parsedText = text;
  //     console.log("Parsed Text", text);
  //     await worker.terminate();
  //   })();
  // }

  upload() {
    this.isFile = !this.isFile;
  }

  fileUpload(file) {
    this.uploadingFile = file.name;
    this.isUploading = true;
    this.requestSubscription = this.api
      .uploadAndProgress(file)
      .subscribe((event) => {
        if (event.type === HttpEventType.UploadProgress) {
          this.percentDone = Math.round((100 * event.loaded) / event.total);
        } else if (event instanceof HttpResponse) {
          this.uploadSuccess = true;
          this.isUploading = false;
        }
      });
  }

  readChequeData(file) {
    const formData = new FormData();
    formData.append('file', file);
    this.api.getChequeData(formData).subscribe((res) => {
      let cheque: any = {};
      res.data.forEach((d) => {
        cheque[d.label] = d.value;
      });

      this.chequeData = cheque;
      // TODO;
      let chequeNo = this.chequeData['chequeNo'];
      chequeNo = chequeNo.substring(1, chequeNo.length - 1);
      this.chequeData['chequeNo'] = chequeNo;
      this.chequeData['name'] = 'Axis Bank Ltd';
      console.log(this.chequeData);
    });
  }

  deleteFile(item: any) {
    var index = this.files.indexOf(item);
    this.files.splice(index, 1);
    this.cdr.markForCheck();
  }

  cancel(item: any) {
    this.requestSubscription.unsubscribe();
    var index = this.files.indexOf(item);
    this.files.splice(index, 1);
    this.cdr.markForCheck();
  }
  async openCropComponent(file) {
    let modal = await this.modalCtrl.create({
      component: CropComponent,
      backdropDismiss: true,
      componentProps: {
        file: file,
      },
    });

    modal.onDidDismiss().then((res) => {
      console.log('Getting Back Data', res);
      const fReader = new FileReader();
      fReader.readAsDataURL(res?.data);
      fReader.onloadend = (_event: any) => {
        this.filename = file.name;
        this.base64File = _event.target.result;
        console.log(this.filename);
        this.files.push(file);
        this.readChequeData(file);
        // this.imageParser(file);
        this.fileUpload(file);
      };
    });

    return await modal.present();
  }
}
